<template>
<div>
    <loader :is-visible="isLoading"></loader>
    <section class="main-content">
        <div ref="hellp" id="divMaindealData">
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{ $t('ManageOffboardEmployee') }}</span>
                        <span class="float-right">
                            <a href="javascript:;" class="p-action-btn text-white" :title="$t('BacktoList')" @click="Close">
                                <em class="fa fa-angle-double-left pr-2" aria-hidden="true"></em>
                                <br />{{ $t('BacktoList') }}
                            </a>
                        </span>
                    </div>
                </div>
            </div>
            <div class="border p-3">
                <Form ref="JobPostForm">
                    <dynamic-form ref="AddEmployee" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit" :mandatorytext='MandatoryString'>
                    </dynamic-form>
                </Form>
            </div>
        </div>
    </section>
</div>
</template>

<script>
import DataService from '../../../services/DataService'
export default {
    data() {
        return {
            isLoading: false,
            EmployeeData: [],
            UserData: [],
            EmployeeExitId: 0,
            EditEmployeeData: [],
            MandatoryString:'Fields marked with an asterisk (*) are mandatory.',
            FormSchema: [{
                    layoutType: "triple",
                    group_name: 'Separation Form',
                    group_display_name: this.$t('SeparationForm'),
                    Data: [{
                            astype: "SelectField",
                            label: this.$t('EmployeeName'),
                            name: "EmployeeName",
                            value: "",
                            mode: "tag",
                            config: {
                                options: [],
                                onChange: this.onChangeEmployeeName,
                            },
                            visibility: true,
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('EmployeeId'),
                            name: "EmployeeId",
                            value: "",
                            validationRules: {
                                "required": false,
                                max: 100
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Email'),
                            name: "Email",
                            value: "",
                            validationRules: {
                                "required": false,
                                max: 100
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Department'),
                            name: "Department",
                            value: "",
                            validationRules: {
                                "required": false,
                                max: 100
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Designation'),
                            name: "Designation",
                            value: "",
                            validationRules: {
                                "required": false,
                                max: 100
                            },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('Reason'),
                        name: "Reason",
                        value: "",
                        validationRules: "",
                    }]
                },
                {
                    layoutType: "triple",
                    group_name: 'Employee Checklist',
                    group_display_name: this.$t('EmployeeChecklist'),
                    Data: [{
                        astype: "RadioField",
                        label: this.$t('AllTaskCompleted'),
                        name: "AllTaskCompleted",
                        value: "0",
                        placeholder: "",
                        config: {
                            options: [{ value: "1", name: "Yes" }, { value: "0", name: "No" }]
                        },
                        visibility: true,
                        validationRules: {
                            "required": false
                        },
                    },
                    {
                        astype: "RadioField",
                        label: this.$t('KnowledgeTransferred'),
                        name: "KnowledgeTransferred",
                        value: "0",
                        placeholder: "",
                        config: {
                            options: [{ value: "1", name: "Yes" }, { value: "0", name: "No" }]
                        },
                        visibility: true,
                        validationRules: {
                            "required": false
                        },
                    },
                    {
                        astype: "RadioField",
                        label: this.$t('DocumentTransferred'),
                        name: "DocumentTransferred",
                        value: "0",
                        placeholder: "",
                        config: {
                            options: [{ value: "1", name: "Yes" }, { value: "0", name: "No" }]
                        },
                        visibility: true,
                        validationRules: {
                            "required": false
                        },
                    },
                    {
                        astype: "RadioField",
                        label: this.$t('StaffIdentificationCardCollected'),
                        name: "StaffIdentificationCardCollected",
                        value: "0",
                        placeholder: "",
                        config: {
                            options: [{ value: "1", name: "Yes" }, { value: "0", name: "No" }]
                        },
                        visibility: true,
                        validationRules: {
                            "required": false
                        },
                    }]
                },
                {
                    layoutType: "triple",
                    group_name: 'IT Department',
                    group_display_name: this.$t('ITDepartment'),
                    Data: [{
                        astype: "RadioField",
                        label: this.$t('EmailAccessUpdated'),
                        name: "EmailAccessUpdated",
                        value: "0",
                        placeholder: "",
                        config: {
                            options: [{ value: "1", name: "Yes" }, { value: "0", name: "No" }]
                        },
                        visibility: true,
                        validationRules: {
                            "required": false
                        },
                    },
                    {
                        astype: "RadioField",
                        label: this.$t('ITAssetsVerified'),
                        name: "ITAssetsVerified",
                        value: "0",
                        placeholder: "",
                        config: {
                            options: [{ value: "1", name: "Yes" }, { value: "0", name: "No" }]
                        },
                        visibility: true,
                        validationRules: {
                            "required": false
                        },
                    }]
                },
                {
                    layoutType: "triple",
                    group_name: 'Account Department',
                    group_display_name: this.$t('AccountDepartment'),
                    Data: [{
                        astype: "RadioField",
                        label: this.$t('LeaveBalanceVerified'),
                        name: "LeaveBalanceVerified",
                        value: "0",
                        placeholder: "",
                        config: {
                            options: [{ value: "1", name: "Yes" }, { value: "0", name: "No" }]
                        },
                        visibility: true,
                        validationRules: {
                            "required": false
                        },
                    },
                    {
                        astype: "RadioField",
                        label: this.$t('AnyDues'),
                        name: "AnyDues",
                        value: "0",
                        placeholder: "",
                        config: {
                            options: [{ value: "1", name: "Yes" }, { value: "0", name: "No" }]
                        },
                        visibility: true,
                        validationRules: {
                            "required": false
                        },
                    },
                    {
                        astype: "RadioField",
                        label: this.$t('FinalPaymentVerified'),
                        name: "FinalPaymentVerified",
                        value: "0",
                        placeholder: "",
                        config: {
                            options: [{ value: "1", name: "Yes" }, { value: "0", name: "No" }]
                        },
                        visibility: true,
                        validationRules: {
                            "required": false
                        },
                    }]
                }
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,

                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ]
        }
    },
    created: function () {
        var vm = this;
        vm.GetUserList();
        vm.EmployeeExitId = this.$route.params.id;
        if(vm.EmployeeExitId > 0){
            vm.EditEmployee();
        }
    },
    methods: {
        EditEmployee() {
            var vm = this;
            var params = `Id=${vm.EmployeeExitId}`
            DataService.GetEmployeeExitDetails(params).then((response) => {                
                if(response.data.status = 'Success'){                   
                    vm.EditEmployeeData = response.data.result[0];
                    if(vm.EditEmployeeData.EMPLOYEE_EXIT_ID !=null){                        
                        vm.EditEmployeeName(vm.EditEmployeeData.EMPLOYEE_EXIT_ID);
                    }
                    vm.FormSchema[0].Data[1].value = vm.EditEmployeeData.EMPLOYEE_ID == null ? '--' : vm.EditEmployeeData.EMPLOYEE_ID; 
                    vm.FormSchema[1].Data[0].value = vm.EditEmployeeData.REASON; 
                    vm.FormSchema[2].Data[0].value = vm.EditEmployeeData.IS_ALL_TASK_COMPLETED == true ? 1 : 0; 
                    vm.FormSchema[2].Data[1].value = vm.EditEmployeeData.IS_KNOWLEDGE_TRANSFERRED == true ? 1 : 0; 
                    vm.FormSchema[2].Data[2].value = vm.EditEmployeeData.IS_DOCUMENT_TRANSFERRED == true ? 1 : 0; 
                    vm.FormSchema[2].Data[3].value = vm.EditEmployeeData.IS_STAFF_IDENTIFICATION_CARD_COLLECTED == true ? 1 : 0; 
                    vm.FormSchema[3].Data[0].value = vm.EditEmployeeData.IS_EMAIL_ACCESS_UPDATED == true ? 1 : 0; 
                    vm.FormSchema[3].Data[1].value = vm.EditEmployeeData.IS_IT_ASSETS_VERIFIED == true ? 1 : 0; 
                    vm.FormSchema[4].Data[0].value = vm.EditEmployeeData.IS_LEAVE_BALANCE_VARIFIED == true ? 1 : 0; 
                    vm.FormSchema[4].Data[1].value = vm.EditEmployeeData.IS_ANY_DUES == true ? 1 : 0; 
                    vm.FormSchema[4].Data[2].value = vm.EditEmployeeData.IS_FINAL_PAYMENT_VARIFIED == true ? 1 : 0;  
                }
            })
        },
        GetUserList() {
            var vm = this;
            var params = `projectId=&userType=&isActive=1&ticketId=0&moduleName=OffboardEmployee`;
            DataService.GetUserList(params).then((response) => {
                if (response.data != '') {
                    vm.EmployeeData = response.data;
                    vm.EmployeeData.forEach(item => {
                        vm.FormSchema[0].Data[0].config.options.push({
                            name: item.USERNAME,
                            value: item.USERID
                        })
                    })
                }
                vm.$refs.AddEmployee.UpdateKeyValue();
            });
        },       
        onChangeEmployeeName: function (id) {
            var vm = this;
            var userId = id.currentTarget._vOptions[1];
            var params = `UserId=${userId}`
            DataService.GetUserDetailsByUserId(params).then((response) => {
                if(response.data != ''){
                    vm.UserData = response.data[0]; 
                    vm.FormSchema[0].Data[1].value = vm.UserData.user_emp_id == null ? '--' : vm.UserData.user_emp_id; 
                    vm.FormSchema[0].Data[2].value = vm.UserData.email_id; 
                    vm.FormSchema[0].Data[3].value = vm.UserData.DEPARTMENT_NAME == null ? '--' : vm.UserData.DEPARTMENT_NAME; 
                    vm.FormSchema[0].Data[4].value = vm.UserData.DESIGNATION_NAME == null ? '--' : vm.UserData.DESIGNATION_NAME; 
                    vm.FormSchema[1].Data[0].value = vm.UserData.reason; 
                    vm.FormSchema[2].Data[0].value = vm.UserData.is_all_task_completed == true ? 1 : 0; 
                    vm.FormSchema[2].Data[1].value = vm.UserData.is_knowledge_transferred == true ? 1 : 0; 
                    vm.FormSchema[2].Data[2].value = vm.UserData.is_document_transferred == true ? 1 : 0; 
                    vm.FormSchema[2].Data[3].value = vm.UserData.is_staff_identification_card_collected == true ? 1 : 0; 
                    vm.FormSchema[3].Data[0].value = vm.UserData.is_email_access_updated == true ? 1 : 0; 
                    vm.FormSchema[3].Data[1].value = vm.UserData.is_it_assets_verified == true ? 1 : 0; 
                    vm.FormSchema[4].Data[0].value = vm.UserData.is_leave_balance_varified == true ? 1 : 0; 
                    vm.FormSchema[4].Data[1].value = vm.UserData.is_any_dues == true ? 1 : 0; 
                    vm.FormSchema[4].Data[2].value = vm.UserData.is_final_payment_varified == true ? 1 : 0; 
                }
            })
        },
        EditEmployeeName: function (id) {
            var vm = this;
            var userId = id;
            vm.FormSchema[0].Data[0].value=id;
            var params = `UserId=${userId}`
            DataService.GetUserDetailsByUserId(params).then((response) => {
                if(response.data != ''){
                    vm.UserData = response.data[0]; 
                    vm.FormSchema[0].Data[1].value = vm.UserData.user_emp_id == null ? '--' : vm.UserData.user_emp_id; 
                    vm.FormSchema[0].Data[2].value = vm.UserData.email_id; 
                    vm.FormSchema[0].Data[3].value = vm.UserData.DEPARTMENT_NAME == null ? '--' : vm.UserData.DEPARTMENT_NAME; 
                    vm.FormSchema[0].Data[4].value = vm.UserData.DESIGNATION_NAME == null ? '--' : vm.UserData.DESIGNATION_NAME;                     
                }
                vm.$refs.AddEmployee.UpdateKeyValue();
            })
        },
        Close: function () {
            this.$router.push({
                name: 'EmployeeExitListing'
            });
        },
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                EmpExitId: vm.$parent.EmployeeExitId == undefined ? 0 : vm.$parent.EmployeeExitId,
                EmployeeId: formfields.EmployeeName,
                AllTaskCompleted: formfields.AllTaskCompleted == true ? '1' : '0',
                KnowledgeTransferred: formfields.KnowledgeTransferred == true ? '1' : '0',
                DocumnetTransferred: formfields.DocumentTransferred == true ? '1' : '0',
                EmailAccessUpdated: formfields.EmailAccessUpdated == true ? '1' : '0',
                ItAssetsVerified: formfields.ITAssetsVerified == true ? '1' : '0',
                LeaveBalanceVarified: formfields.LeaveBalanceVerified == true ? '1' : '0',
                AnyDues: formfields.AnyDues == true ? '1' : '0',
                FinalPaymentVarified: formfields.FinalPaymentVerified == true ? '1' : '0',
                Reason: formfields.Reason,
                IsStaffIdentification: formfields.StaffIdentificationCardCollected == true ? '1' : '0'
            };
            var params = JSON.stringify(jsonobj);
            DataService.SaveExitEmployee(params).then((response) => {
                if (response.data[0].Column1 = 1){
                    vm.ShowAlert((vm.$t('SaveoffboardEmployee')), "success", true, vm.$t("Alert"));
                    vm.Close();
                } else if (response.data[0].Column1 = 2) {
                    vm.ShowAlert((vm.$t('UpdateExit')), "success", true, vm.$t("Alert"));
                    vm.Close();
                } else {
                    vm.ShowAlert((vm.$t("Exit")), "failure", true, vm.$t("Alert"));
                }
            });
        }
    }
}
</script>
